import React, { useState, useEffect } from 'react'
import TimePicker from 'rc-time-picker';

import { ScheduleDayContainer } from '../../styles/Doctor'
import { BtnCircle, BtnContainer } from '../../styles/User'

function Day({ day, values, setValues, addRow, removeRow, toShow, width, idx, clearDay }) {

    const [work, setWork] = useState(true)

    const handleBtnAdd = () => {
        if(work){ addRow(day) }
    }
    const handleBtnRemove = () => {
        if(work){ removeRow(day) }
    }

    const handlePickerStart = (vPicker, idx) => {
        let newValues = [...values]
        newValues[idx].start = vPicker
        setValues(newValues)
    }

    const handlePickerEnd = (vPicker, idx) => {
        let newValues = [...values]
        newValues[idx].end = vPicker
        setValues(newValues)
    }

    const handleClickHeader = () => {
        clearDay(!work, day)
        setWork(!work)
    }

    useEffect(() => {
        if(values.length !== 0){
            if(new Date(values[0].start).getHours() === 0 && new Date(values[0].end).getHours() === 0){ setWork(false) }
        }
    }, [values])

    return (
        <ScheduleDayContainer 
            width={width} 
            bgColor={idx % 2 === 0 ? 'rgb(235,235,235)' : 'rgb(245,245,245)'}
            bgColorHeader={work ? 'rgb(0,47,91)' : 'rgb(235,235,235)'}
        >
            <div className="header" onClick={handleClickHeader}>{day}</div>
            {values?.map((value, idx) => (
                toShow ? (
                    <div className="row" key={idx}>
                        {value.inicio === "00:00" && value.final === "00:00" ? (
                            <>
                            <span>No</span>
                            <span>trabaja</span>
                            </>
                        ) : (
                            <>
                            <span>{value.inicio}</span>
                            <span className="span-to-show">-</span>
                            <span>{value.final}</span>
                            </>
                        )}
                        
                    </div>
                ):(
                    <div className="row" key={idx}>
                        <TimePicker
                            value={value.start}
                            onChange={(vPicker) => {handlePickerStart(vPicker, idx)}}
                            showSecond={false}
                            minuteStep={30}
                            className="picker"
                        />
                        <span>-</span>
                        <TimePicker
                            value={value.end}
                            onChange={(vPicker) => {handlePickerEnd(vPicker, idx)}}
                            showSecond={false}
                            minuteStep={30}
                            className="picker"
                            onSubmit={() => { console.log('a') }}
                        />
                    </div>
                )))}
            {!toShow && (
                <BtnContainer>
                    <BtnCircle onClick={handleBtnRemove} mLeft="0">-</BtnCircle>
                    <BtnCircle onClick={handleBtnAdd} mLeft="0">+</BtnCircle>
                </BtnContainer>
            )}
        </ScheduleDayContainer>
    )
}

export default Day
