import React, { useState, useEffect } from 'react'
import { faChevronDown, faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import DoctorItem from './DoctorItem'

import { DoctorsGroupContainer, DoctorsTitleContainer, DoctorsTitleButton, DoctorsGroupContentContainer } from '../../styles/Doctors'

import { maxWidthMobile } from '../../variables'

const DoctorsGroup = ({ doctors, title, closed, woDescription, width, itemWidth, handleItem, doctorSelected, height, date, dateType, userType }) => {

    let interval = null

    const [showContent, setShowContent] = useState(true)
    const [changeIcon, setChangeIcon] = useState(true)
    const [render, setRender] = useState(false)
    
    const handleBtnShowContent = (e) => {
        e.preventDefault()
        setShowContent(!showContent)
    }

    const getHeight = () => {
        setTimeout(() => setRender(!render), 100);
        if(window.screen.width <= maxWidthMobile){ 
            return `${Math.ceil(doctors.length * 24)}vw` 
        }
        else{ 
            return `${Math.ceil(doctors.length / (itemWidth/15)) * 10}vw` 
        }
    }

    useEffect(() => {
        if(closed){
            setShowContent(false)
            setChangeIcon(false)
        }
    }, [closed])

    useEffect(() => {
        interval = setTimeout(() => setChangeIcon(showContent), 500);
        window.addEventListener('resize', getHeight)
        return () => {
            clearTimeout(interval)
            window.removeEventListener('resize', getHeight)
        }
    }, [showContent])

    return (
        <DoctorsGroupContainer width={width} height={height}> 
            {title && (
                <DoctorsTitleContainer>
                    <h1>{title}</h1>
                    <DoctorsTitleButton onClick={handleBtnShowContent}>
                        <FontAwesomeIcon 
                            icon={changeIcon ? faChevronDown : faChevronLeft} 
                            color="rgb(0,47,91)" 
                            size="2x" 
                        />
                    </DoctorsTitleButton>
                </DoctorsTitleContainer>
            )}
            {date && (
                <div className="message">Si el {dateType === "medico" ? 'doctor' : 'quimico'} que buscas no está a continuación significa que no trabaja en el horario que has escogido</div>
            )}
            <DoctorsGroupContentContainer 
                height={height ? 'fit-content' : showContent ? getHeight() : '0'}
                width={width}
            >
                {doctors.map( doctor => (
                    <DoctorItem 
                        doctor={doctor}
                        onClick={handleItem}
                        key={doctor.id}
                        woDescription={woDescription}
                        itemWidth={itemWidth}
                        doctorSelected={doctorSelected}
                        dateType={dateType}
                        userType={userType}
                    />
                ))}
            </DoctorsGroupContentContainer>
        </DoctorsGroupContainer>
        
    )
}

export default DoctorsGroup
