import React, { useState, useEffect, useContext } from 'react'
import axios from 'axios'

import DoctorsGroup from '../doctors/DoctorsGroup'
import DoctorsFilter from './DoctorsFilter'

import { URL } from '../../variables'
import { appContext } from '../../context/Provider'

function Doctors() {

    const context = useContext(appContext)

    const [doctors, setDoctors] = useState([])
    const [doctorsShowing, setDoctorsShowing] = useState([])
    const [schedule, setSchedule] = useState([])

    const getDoctors = async () => {
        let res = await axios({
            method: 'GET',
            url: `${URL}/all-medicos?schedule=true`
        })
        return res.data
    }

    const selectDoctor = doctor => context.dispatchDoctorDate({ type: 'SET', value: doctor })

    const dateInPeriod = (schedule, date) => {
        let okDate = false
        for(const period of schedule){
            const hourStart = Number(period.inicio.split(':')[0])
            const minuteStart = Number(period.inicio.split(':')[1])
            const hourEnd = Number(period.final.split(':')[0])
            const minuteEnd = Number(period.final.split(':')[1])
    
            const dateHours = date.getHours()
            const dateMinuttes = date.getMinutes()
    
            if(!((dateHours === hourEnd && dateMinuttes === minuteEnd) || dateHours > hourEnd || (dateHours === hourEnd && dateMinuttes > minuteEnd) ||
            (dateHours === hourStart && dateMinuttes < minuteStart) || dateHours < hourStart)){ okDate = true }
        }
        return okDate
    }

    useEffect(() => {
        if(context.doctorsDate.length === 0 && context.scheduleLabs.length === 0){
            getDoctors().then( ({doctors, schedule}) => {
                setDoctors(doctors)
                setDoctorsShowing(doctors)
                setSchedule(schedule)
            }).catch( (e) => {
                context.dispatchErr({ type: 'SET', value: true })
                context.dispatchMessage({ type: 'SET', value: 'Ha oucrrido un error intentando obtener la lista de doctores. Itentanlo más tarde.' })
            })
        }else{
            setDoctors(context.doctorsDate)
            setDoctorsShowing(context.doctorsDate)
            setSchedule(context.scheduleLabs)
        }
    }, [context.doctorsDate, context.scheduleLabs])

    useEffect(() => {
        const date = new Date(context.date)
        if(doctors.length !== 0 && schedule.length !== 0){
            let docs = doctors.filter( doctor => {
                let sch = schedule.find( item => (item.medico === doctor.id || item.usuario === doctor.id) && item.dia === date.getDay())
                if(!sch){ return false }
                return dateInPeriod([sch], date)
            })
            setDoctorsShowing(docs)
        }
    }, [context.date, doctors, schedule])

    return (
        <>
        <DoctorsFilter 
            doctors={doctors}
            setDoctors={setDoctorsShowing}
            dateType={context.dateType}
        />
        <DoctorsGroup 
            doctors={doctorsShowing}
            woDescription
            width="100%"
            itemWidth={24}
            handleItem={selectDoctor}
            doctorSelected={context.doctorDate}
            height="100%"
            date
            dateType={context.dateType}
        />
        </>
    )
}

export default Doctors
