import React, { useReducer } from 'react'

import { defaultReducer, addRemoveReducer } from './reducers'

export const appContext = React.createContext()

function Provider({ children }) {

    const [date, dispatchDate] = useReducer(defaultReducer, null)
    const [dateType, dispatchDateType] = useReducer(defaultReducer, 'medico')
    const [message, dispatchMessage] = useReducer(defaultReducer, '')
    const [doctorDate, dispatchDoctorDate] = useReducer(defaultReducer, {})
    const [labsDate, dispatchLabsDate] = useReducer(addRemoveReducer, [])
    const [doctorsDate, dispatchDoctorsDate] = useReducer(defaultReducer, [])
    const [scheduleLabs, dispatchScheduleLabs] = useReducer(defaultReducer, [])
    const [err, dispatchErr] = useReducer(defaultReducer, false)

    return (
        <appContext.Provider
            value={{
                date,
                dateType,
                doctorDate,
                labsDate,
                doctorsDate,
                scheduleLabs,
                err,
                message,
                dispatchDate,
                dispatchDateType,
                dispatchDoctorDate,
                dispatchLabsDate,
                dispatchDoctorsDate,
                dispatchScheduleLabs,
                dispatchMessage,
                dispatchErr
            }}
        >
            {children}
        </appContext.Provider>
    )
}

export default Provider
