import React, { useEffect, useState, useRef } from 'react'
import axios from 'axios'
import Loader from 'react-loader-spinner'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCamera } from "@fortawesome/free-solid-svg-icons";

import Input from '../signup/Input'

import { InputImage, Image, ContainerForm, InfoContainer, BtnContainer, EditBtn } from '../../styles/User'

import { URL as URLSERVER } from '../../variables'

import { ReactComponent as UserIcon } from '../../assets/icons/usuario_imagen_default.svg';

function Form({ prevName, prevType, prevDescription, setMessage, setErr, prevImage, prevFacebook, prevInstagram, setSessionExpirate, prevPhone, userType, doctor }) {

    const refInputFile = useRef(null)

    const [name, setName] = useState('')
    const [type, setType] = useState('')
    const [description, setDescription] = useState('')
    const [facebook, setFacebook] = useState('')
    const [instagram, setInstagram] = useState('')
    const [phone, setPhone] = useState('')
    const [imageURL, setImageURL] = useState(null)
    const [image, setImage] = useState(null)
    const [loading, setLoading] = useState(false)

    const handleUploadImage = () => refInputFile.current.click()

    const handleInputImage = e => { 
        if(e.target.files[0].size > 5000000){
            setErr(true)
            return setMessage('LA IMAGEN DEBE PESAR MENOS DE 5MB')
        }
        if(image){ URL.revokeObjectURL(imageURL) }
        setImage(e.target.files[0])
        setImageURL(URL.createObjectURL(e.target.files[0]))
        setErr(false)
    }

    const getFormData = (object) => {
        let formData = new FormData();
        if(image){ formData.append('image', image) }
        Object.keys(object).forEach(key => formData.append(key, object[key]));
        return formData;
    }

    const checkData = () => {
        if(name !== "" && type !== "" && (image || imageURL) &&  ((description !== "" && userType === "MEDICO") || userType !== "MEDICO")){ return true }
        setErr(true)
        if(name === ""){ setMessage('Se tiene que completar el campo nombre.') }
        if(type === ""){ setMessage('Se tiene que completar el campo especialidad.') }
        if(description === ""){ setMessage('Se tiene que completar el campo descripción.') }
        if(!image && !imageURL){ setMessage('Se tiene que poner una imágen.') }
        return false
    }

    const saveInfo = async (data) => {
        const res = await axios({
            url: `${URLSERVER}/admin/user${doctor ? '?setDoctor=true' : ''}`,
            method: 'PUT',
            data
        })
        return res.data
    } 

    const handleBtn = (e) => {
        e.preventDefault()
        if(checkData() && !loading){
            const data = getFormData({ name, type, description, facebook, instagram, phone })
            setLoading(true)
            setErr(false)
            saveInfo(data).then(() => {
                setLoading(false)
                window.location.reload()
            }).catch(e => {
                console.log(e)
                setLoading(false)
                if(e.response?.status === 401){ return setSessionExpirate(true) }
                setErr(true)
                setMessage('No ha sido posible actualizar la información debido a un error en el servidor. Intentalo más tarde o reporta el problema.')
            })
        }
    }

    useEffect(() => {
        if(prevName){ setName(prevName) }
        if(prevType){ setType(prevType) }
        if(prevDescription){ setDescription(prevDescription) }
        if(prevImage){ setImageURL(prevImage) }
        if(prevFacebook){ setFacebook(prevFacebook) }
        if(prevInstagram){ setInstagram(prevInstagram) }
        if(prevPhone){ setPhone(prevPhone) }
    }, [prevName, prevType, prevDescription, prevImage, prevFacebook, prevInstagram, prevPhone])

    return (
        <ContainerForm width="80%">
            {(image || imageURL) ? (
                <InputImage onClick={handleUploadImage} bgColor='transparent'>
                    <Image src={imageURL || image}/>
                    <div className="camera">
                        <FontAwesomeIcon icon={faCamera} color="rgb(0,47,91)" />
                    </div>
                </InputImage>
            ):(
                <InputImage onClick={handleUploadImage} bgColor='transparent'>
                    <div className="icon-container">
                        <UserIcon width="10vw" height="10vw"/>
                    </div>
                    <div className="camera">
                        <FontAwesomeIcon icon={faCamera} color="rgb(0,47,91)" />
                    </div>
                </InputImage> 
            )}
            <InfoContainer width="60%">
                <Input 
                    label="Nombre: "
                    value={name}
                    setValue={setName}
                    id="name"
                />
                <Input 
                    label="Especialidad: "
                    value={type}
                    setValue={setType}
                    id="type"
                />
                {(userType !== "ASISTENTE" || doctor) && (
                    <>
                    <Input 
                        label="Teléfono: "
                        value={phone}
                        setValue={setPhone}
                        id="phone"
                    />
                    <Input 
                        label="Descripción: "
                        value={description}
                        setValue={setDescription}
                        id="description"
                        textarea
                        height="8vw"
                    />
                    <Input 
                        label="Facebook: "
                        value={facebook}
                        setValue={setFacebook}
                        id="facebook"
                    />
                    <Input 
                        label="Instagram: "
                        value={instagram}
                        setValue={setInstagram}
                        id="instagram"
                    />
                    </>
                )}
                
                <input 
                    style={{ display: 'none' }}
                    type="file"
                    ref={refInputFile}
                    accept="image/*"
                    name="image"
                    onChange={handleInputImage}
                />
                <BtnContainer>
                    {loading && <Loader type="TailSpin" color={"rgb(0,47,91)"} height={50}/>}
                    <EditBtn onClick={handleBtn} margin="0">GUARDAR</EditBtn>
                </BtnContainer>
            </InfoContainer>
        </ContainerForm>
    )
}

export default Form
