import React, { useState, useContext } from 'react'

import UserForm from './UserForm'
import Calendar from './Calendar'
import ItemsGroup from './ItemsGroup'

import { FormGroupContainer, BtnsCarrouselContainer, BtnCarrousel, FormGroupItemsContainer } from '../../styles/Date'

import { appContext } from '../../context/Provider'

function FormGroup({ dateItem, dateType, currentIdx, setCurrentIdx, setDateType }) {

    const context = useContext(appContext)
    
    const maxNumber = () => {
        if(JSON.stringify(dateItem) === "{}"){
            if(context.dateType === "laboratorio"){ return 3 }
            else{ return 2 }
        }else{
            if(context.dateType === "laboratorio"){ return 2 }
            else{ return 1 }
        }
    }

    const next = () => {
        if(JSON.stringify(dateItem) === "{}"){
            if(context.dateType === "laboratorio"){  
                if(currentIdx !== 3){ setCurrentIdx(currentIdx +1 ) } 
            }else{
                if(currentIdx !== 2){ setCurrentIdx(currentIdx +1 ) } 
            }
        }else{
            if(context.dateType === "laboratorio"){  
                if(currentIdx !== 2){ setCurrentIdx(currentIdx +1 ) } 
            }else{
                if(currentIdx !== 1){ setCurrentIdx(currentIdx +1 ) } 
            }
        }
    }

    const back = () => {
        if(currentIdx !== 0){ setCurrentIdx(currentIdx -1 ) } 
    }

    return (
        <FormGroupContainer>
            <FormGroupItemsContainer>
                <Calendar 
                    translateX={currentIdx * -100}
                    dateType={dateType}
                    setDateType={setDateType}
                />
                {JSON.stringify(dateItem) === "{}" && <ItemsGroup dateType={dateType} translateX={currentIdx * -100}/>}
                {context.doctorsDate.length !== 0 && context.dateType === "laboratorio" && <ItemsGroup dateType='medico' translateX={currentIdx * -100}/>}
                <UserForm 
                    dateItem={JSON.stringify(dateItem) !== "{}" ? dateItem : context.doctorDate} 
                    translateX={currentIdx * -100}
                    dateType={dateType}
                    labsDate={context.labsDate}
                />
            </FormGroupItemsContainer>
            <BtnsCarrouselContainer justifyContent={ currentIdx === 0 ? 'flex-end' : currentIdx !== maxNumber() ? 'space-between' : 'flex-start' }>
                {currentIdx !== 0 && <BtnCarrousel onClick={back}>{'<<<'}</BtnCarrousel>}
                {currentIdx !== maxNumber() && <BtnCarrousel onClick={next}>{'>>>'}</BtnCarrousel>}
            </BtnsCarrouselContainer>
        </FormGroupContainer>
    )
}

export default FormGroup
