import React from 'react'

import Doctors from './Doctors'
import Labs from './Labs'

import { GroupContainer } from '../../styles/Date'

function ItemsGroup({ translateX, dateType }) {

    return (
        <GroupContainer 
            translateX={translateX} 
            fDirection="column"
            alignItems="center"
        >
            {dateType === "medico" ? <Doctors /> : <Labs /> }
        </GroupContainer>
    )
}

export default ItemsGroup
